.loader {
  width: 48px;
  height: 48px;
  background: #fff;
  border-radius: 50%;
  animation: skLinRotate 1s ease-in-out infinite alternate;
  position: relative;
}

.loader:after {
  content: "";
  border: 5px solid rgba(0, 0, 0, 0);
  border-top-color: #010101;
  border-radius: 50%;
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px;
}

@keyframes skLinRotate {
  95%, 100% {
    transform: rotate(840deg);
  }
}

.cat-info {
  width: 400px;
}

.breed-select {
  width: 220px;
  height: 30px;
  background-position: 95%;
  border-radius: 5px;
  margin-bottom: 20px;
  margin-right: 2%;
}

select > option {
  height: 31px;
  width: 220px;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  padding-right: 15px;
}

.breed-select > select {
  width: 100%;
  color: #000;
  -webkit-appearance: none;
  -moz-appearance: none;
  background: none;
  border: 0;
  font-family: Helvetica Neue, Helvetica, sans-serif;
  font-size: 14px;
  line-height: 2.5;
  overflow: hidden;
}

